import { z } from 'zod';
import {
  FilterGroupInterface,
  FilterGroupSchema,
} from './filter-group.interface';
import { FilterInterface, FilterSchema } from './filter.interface';

export const FiltersSchema = <z.ZodType<FiltersInterface>>(
  z.record(
    z.union([z.lazy(() => FilterGroupSchema), FilterSchema, z.literal(false)]),
  )
);

export interface FiltersInterface {
  [key: string]: FilterGroupInterface | FilterInterface | false;
}
